import React, { Component } from "react";

import AppHeader from "../app-header";
import TodoList from "../todo-list";
import SearchPanel from "../search-panel";
import ItemStatusFilter from "../item-status-filter";
import AddItem from "../add-item"

import './app.css';

export default class App extends Component {

  constructor() {
    super();
    this.maxId = 100;
    this.state = {
      todoData: [
        this.createItem('Add item')
      ],
      term: '',
      filter: 'all'
    };
  };

  createItem = (label) => {

    const newItem = {
      label,
      important: false,
      done: false,
      id: this.maxId++
    };

    return newItem;
  };

  search = (items, term) => {
    if (term.length === 0) return items;

    return items.filter((el) => {
      return el.label.toLowerCase()
              .indexOf(term.toLowerCase()) > -1;
    })
  };

  onSearchChange = (term) => {
    this.setState({ term });
  }

  deleteItem = (id) => {
    const data = this.state.todoData;
    this.setState(() => {
      return {
        todoData: data.filter((el) => el.id !== id)
      }
    });
  };

  addItem = (label) => {
    
    const newItem = this.createItem(label);

    this.setState(({todoData}) => {
      const newData = [...todoData.slice(), newItem];
      return {
        todoData: newData
      }
    });
  };

  toggleProperty = (arr, id, propName) => {
    const idx = arr.findIndex((el) => el.id === id);

    const oldItem = arr[idx];
    const newItem = { ...oldItem, [propName]: !oldItem[propName]};

    const newArray = [
      ...arr.slice(0, idx),
      newItem,
      ...arr.slice(idx + 1)
    ];

    return newArray;
  };

  onToggleImportant = (id) => {
    this.setState(({todoData}) => {
      const newData = this.toggleProperty(todoData, id, 'important');
      return {
        todoData: newData
      }
    });
  };

  onToggleDone = (id) => {
    this.setState(({ todoData }) => {
      const newData = this.toggleProperty(todoData, id, 'done');
      return {
        todoData: newData
      }
    });
  };

  filter = (items, filter) => {

    if (filter === 'active') {
      const filtered = items.filter((el) => !el.done);
      return filtered;
    }
    if (filter === 'done') {
      const filtered = items.filter((el) => el.done);
      return filtered;
    }
    
    return items;
  }

  onFilter = (filter) => {
    this.setState({ filter });
  }

  render() {
    const { todoData, term, filter } = this.state;

    const visibleItems = this.filter(this.search(todoData, term), this.state.filter);

    const doneCount = visibleItems.filter((el) => el.done).length;
    const todoCount = visibleItems.length - doneCount;
    return (
      <div className="todo-app" >
        <AppHeader toDo={todoCount} done={doneCount} />
        <div className="top-panel d-flex">
          <SearchPanel 
          onSearch={this.onSearchChange}/>
          <ItemStatusFilter 
          filter={filter}
          onFilter={this.onFilter}/>
        </div>

        <TodoList todos={visibleItems}
          onDeleted={this.deleteItem} 
          onToggleImportant={this.onToggleImportant}
          onToggleDone={this.onToggleDone}/>
        <AddItem 
          onAdd={this.addItem}/>
      </div>
    );
  }
};